<template>
  <div class="password-wrap">
    <app-header title="修改密码" showBack="true"></app-header>
    <div class="password-main">
      <mt-field label="手机号码" placeholder="请输入您的手机号码" v-model="tel">
        <mt-button type="default" size="small" @click="send">发送</mt-button>
      </mt-field>
      <mt-field label="验证码" placeholder="请输入您收到的验证码" v-model="code"></mt-field>
      <mt-field label="新密码" type="password" placeholder="请输入您要更改的密码" v-model="pass1"></mt-field>
      <mt-field label="确认密码" type="password" placeholder="请再次输入您要更改的密码" v-model="pass2"></mt-field>
      <mt-button @click="submitPassword" type="primary" size="large" style="margin: 2rem auto;">确认</mt-button>
    </div>
  </div>
</template>

<script>
import AppHeader from '../../components/header/header'
import {modiflyDealerPassword} from '../../api/maintain'
export default {
  name: 'Dealer-Bind-Tel',
  data () {
    return {
      tel: '',
      code: '',
      pass1: '',
      pass2: ''
    }
  },
  methods: {
    send () {
      modiflyDealerPassword(this.tel)
        .then(data => this.$toast(data.errormsg))
        .catch(err => {
          console.log(err)
          this.$toast('网络错误，请重试')
        })
    },
    submitPassword () {
      if (!this.pass1 || !this.pass2 && this.pass1 !== this.pass2) {
        return this.$toast('两次密码输入不一致')
      }

      modiflyDealerPassword(this.tel, this.code, this.pass1)
        .then(data => {
          if (data.state) {
            this.$toast(data.errormsg || '密码修改成功')
            this.$router.back()
          } else {
            this.$toast(data.errormsg || '修改失败')
          }
        })
        .catch(err => {
          console.log(err)
          this.$toast('网络错误，请重试')
        })
    }
  },
  components: { AppHeader }
}
</script>

<style lang="less" scoped>
.password-wrap{
  background: #e5e5e5;
  height: 100%;
  padding: 55px 0;
  overflow-x: hidden;
  overflow-y: scroll;
}
</style>



